<!--导航栏-->
<template>
<div>
    <div class="tradeTop borderBottom">
        <!--左部菜单-->
        <div class="tradeTopLeft">
            <router-link :to="{name:'index'}" class="logo"><img src="@/assets/img/smartbit_logo.png" style="height:30px;object-fit: contain;" alt=""></router-link>
            <!--资产组合，合约交易-->
            <div class="navSpan" style="white-space: nowrap;">
                
                <!-- <Input search placeholder="搜索" style="width:200px;" /> -->
                
                <Poptip v-model="isMenuPop" trigger="hover" placement="bottom-start">
                    <Icon type="md-apps" size="24" class="clof ml5 mr5 pointer" />
                    <template #title>
                        <div class="sub_menu">菜单</div>
                    </template>
                    <template #content>
                        <div class="pt10" style="background: #17171a;">
                            <functional @close="isMenuPop=false"></functional>
                        </div>
                    </template>
                </Poptip>
                <router-link :class="{menuActive:navigationActive==''}" :to="{name:'index'}">首页</router-link>
                <router-link :class="{menuActive:navigationActive=='market_quotation'}" :to="{name:'market_quotation'}">行情</router-link>
                <router-link :class="{menuActive:navigationActive=='trading'}" :to="{name:'trading'}">分析</router-link>
                <!-- <Poptip trigger="hover" placement="bottom-start">
                    <a :class="{menuActive:navigationActive=='early_warn'}" style="margin: 0 5px;padding: 0 15px;">行情预警</a>
                    <template #title>
                        <div class="sub_menu" @click="$router.replace('/amount_warn')">预警</div>
                    </template>
                    <template #content>
                        <div class="sub_menu" @click="$router.replace('/early_warn')">我的关注-雷达</div>
                    </template>
                </Poptip> -->
                <router-link :class="{menuActive:navigationActive=='information'}" :to="{name:'information'}">舆情资讯</router-link>
                <!-- <router-link :class="{menuActive:navigationActive=='early_warn'}" :to="{name:'early_warn'}">我的关注</router-link> -->
                <!-- <router-link :class="{menuActive:navigationActive=='Industry_quotation'}" :to="{name:'Industry_quotation'}">行业报价</router-link> -->
                <router-link :class="{menuActive:navigationActive=='field'}" :to="{name:'field'}">低碳领域</router-link>
            </div>
        </div>
        <!--右部菜单-->
        <div class="tradeTopRight">
            <div v-if="isInitWallet" class="rightMenus alignCenter" style="height: 30px;">
                <div v-if="bindInfo.isVip==2" class="mr10 red active_t" @click="defineRelation()">{{$t('activateVIP')}}</div>
                <div v-if="bindInfo.isVip==1" class="mr10 orange fontb">VIP</div>
            </div>
            <!--反馈-->
            <Tooltip placement="top-end">
                <div slot="content">{{$t('feedback')}}</div>
                <div class="actionWpTitle">
                    <div class="rightMenus">
                        <div class="rightMenuItem" @click="showFeedbackPop()">
                            <img :src="require('@/assets/img/feedback.svg')" style="width:20px;height:20px;opacity:0.5;" />
                        </div>
                    </div>
                </div>
            </Tooltip>
            <!--通知-->
            <Tooltip placement="top-end">
                <div slot="content">{{$t('notice')}}</div>
                <div class="actionWpTitle">
                    <div class="rightMenus">
                        <div class="rightMenuItem" @click="showNoticePop()">
                            <img :src="require('@/assets/img/notice.svg')" style="width:20px;height:20px;opacity:0.5;" />
                        </div>
                    </div>
                </div>
            </Tooltip>
            <!--分享-->
            <Tooltip v-if="isInitWallet" placement="top-end">
                <div slot="content">{{$t('share')}}</div>
                <div class="actionWpTitle">
                    <div class="rightMenus">
                        <div class="rightMenuItem" @click="showSharePop()">
                            <img :src="require('@/assets/img/share.svg')" style="width:20px;height:20px;opacity:0.5;" />
                        </div>
                    </div>
                </div>
            </Tooltip>
            <div class="navSpan">
                <Dropdown>
                    <Button>
                        {{this.$t('language')}}
                        <Icon type="md-arrow-dropdown" />
                    </Button>
                    <DropdownMenu slot="list">
                        <DropdownItem @click.native="changeLanguage('en-US')">English</DropdownItem>
                        <DropdownItem @click.native="changeLanguage('zh-CN')">中文</DropdownItem>
                    </DropdownMenu>
                </Dropdown>
            </div>
            <!--钱包菜单-->
            <div v-if="token" class="walletAddress alignCenter mr10">
                <Dropdown placement="bottom-end">
                    <Button class="alignCenter">
                        <span class="alignCenter">
                            <img class="exit1" :src='require("@/assets/img/user.png")' style="width:30px;height:30px;" />
                            <!-- <img class="tradeTypeIcon ml10" :src="require('@/assets/img/'+tradeTypeIcon)" alt=""> -->
                            <span class="ml10">{{info.nickname||'用户'}}</span>
                            <Icon type="md-arrow-dropdown" />
                        </span>
                    </Button>
                    <DropdownMenu slot="list">
                        <DropdownItem>
                            <div @click="$router.push('/early_warn')" class="menus"><Icon type="ios-bookmark-outline" size="20" class="mr10"></Icon>{{$t('我的关注 - 雷达')}}</div>
                        </DropdownItem>
                        <!-- <DropdownItem>
                            <div class="menus"><Icon type="ios-cloud-outline" size="20" class="mr10"></Icon>在Etherscan中开仓</div>
                        </DropdownItem> -->
                        <DropdownItem>
                            <div @click="exitConnect()" class="exit_hover">
                                <img class="exit1 mr10" :src='require("@/assets/img/exit.svg")' style="width:20px;height:20px;" />
                                <img class="exit2 mr10" :src='require("@/assets/img/exit_active.svg")' style="width:20px;height:20px;" />{{$t('退出登录')}}
                            </div>
                        </DropdownItem>
                    </DropdownMenu>
                </Dropdown>
            </div>
            <!--连接钱包-->
            <div v-else class="ml10 mr10">
                <Button type="info" @click="isconnectWallet=true" style="min-width:120px;">登录</Button>
            </div>
        </div>
    </div>
    <!--分享弹窗-->
    <Modal  v-model="isSharePop" width="400px" :closable="false">
        <!--标题-->
        <template slot="header">
            <span></span>
        </template>
        <!--内容-->
        <div>
            <div class="flexCenter mt10">
                <vue-qr :text="myShareLink"
                        :correctLevel="3"
                        :size="250"
                        :margin="10"
                        colorDark="#000000"
                        colorLight="#ffffff"
                        backgroundColor="white"
                        backgroundDimming="white"
                        :logoSrc="require('@/assets/img/smartbit_icon.png')"
                        :logoScale=".2"
                        :logoMargin="5"
                        logoBackgroundColor="white"
                ></vue-qr>
            </div>
            <div class="mt20 center">{{$t('promotionRewardDesc')}}</div>
            <div class="mt15 actionWpTitle">{{$t('promotionLink')}}</div>
            <div class="mt10">
                <Input :value="myShareLink" type="text" placeholder="" max-length="50" readonly />
            </div>
            <div class="mt15 actionWpTitle">{{$t('promotionPreference')}}</div>
            <div class="amountBox">
                <div>{{$t('discountOne')}}</div>
                <div>{{$t('discountTwo')}}</div>
            </div>
        </div>
        <!--按钮-->
        <template slot="footer">
            <Button type="info" size="large" long @click="copyCode()">{{$t('copyLink')}}</Button>
        </template>
    </Modal>
    <!--连接钱包弹窗-->
    <ConnectWallet v-model="isconnectWallet" @loginIn="loginIn"></ConnectWallet>
    <!--反馈弹窗-->
    <Modal v-model="isFeedbackPop" width="400px" :mask-closable="false">
        <template slot="header">
            <span>{{$t('feedback')}}</span>
        </template>
        <div class="amountBox">
            <div class="flexBetween">
                <textarea v-model="feedback" rows="8" class="textareaStyle" style="width:100%;" maxLength="1000" :placeholder="$t('feedDetail')"></textarea>
            </div>
        </div>
        <template slot="footer">
            <Button type="info" size="large" long @click="sendFeedback()" :disabled="!feedback">
                <span>{{$t('send')}}</span>
            </Button>
        </template>
    </Modal>
</div>
</template>

<script>
import ConnectWallet from "@/components/model/connectWallet.vue"
import exInput from "@/components/common/exInput/index.vue"
import { getInvitation,getAddressByInvitation,getRelation,defineRelation,sendMsg } from "@/api/http"
import vueQr from "vue-qr"; //引入二维码生成组件
import config from "@/config";
//
// import Web3 from "web3";
// import Exchange from "@/utils/build/contracts/Exchange.json"
// import { default as contract } from 'truffle-contract';
// import ContractUtil from "@/utils/contractUtil"
// var exchange;
// var web3Provider;
import functional from '@/views/index/components/functional'
let loading=false;
export default {
    components: {
       ConnectWallet,exInput,vueQr,functional
    },
    data () {
        return {
            tradeTypeIcon:'',//图标
            isconnectWallet:false,//连接钱包弹窗
            isSharePop:false,//分享弹窗
            isFeedbackPop:false,//反馈
            navigationActive:'',//活动菜单项
            form:{
                code:''
            },
            myShareLink:'',//分享链接
            bindInfo:{
                isVip:'',//是否绑定。1：已激活，2：未激活；其他未绑定。
            },//绑定上下级信息
            feedback:'',//反馈
            token:localStorage.getItem('token'),
            info:{},
            isMenuPop:false,
        };
    },
    mounted () {
        console.log('token:',localStorage.getItem('token'))
    },
    methods: {
        //获取用户信息
        user_detail(){
            this.$api.user_detail({}).then(res => {
                this.info=res||{};
            });
        },
        //登录
        loginIn(){
            this.token=localStorage.getItem('token')
        },
        //通知后端激活成功
        sendDefineRelation(){
            defineRelation({
                address:this.addr
            }).then(res=>{
                if(res.code == 200){
                }else{
                    // this.$Message.error(this.$t('operationFailed'));
                }
            }).catch(err=>{
                console.log(err);
            });
        },
        //显示分享弹窗，获取推广码
        showSharePop(){
            this.isSharePop=true;
        },
        //显示通知弹窗
        showNoticePop(){
            this.$emit('showNoticePop',true);
        },
        //显示反馈弹窗
        showFeedbackPop(){
            this.isFeedbackPop=true;
        },
        //发送反馈
        sendFeedback(){
            this.feedback='';
            this.isFeedbackPop=false;
            this.$Message.warning('感谢您的反馈，我们会尽快关注您的反馈内容！');
            // this.$Message.warning(this.$t('underConstruction'));
        },
        //断开连接
        exitConnect(){
            // this.$store.commit("exitLogin");
            localStorage.removeItem('token')
            this.token='';
        },
        //复制推广码
        copyCode(){
            this.$copyText(this.myShareLink).then((e)=>{
                this.$Message.success(this.$t('copySuccess'));
            }, function (e) {
                //this.$Message.error(this.$t('operationFailed'));
            })
        },
        //复制地址
        copyAddr(){
            this.$copyText(this.addr).then((e)=>{
                this.$Message.success(this.$t('copySuccess'));
            }, function (e) {
                // this.$Message.error(this.$t('operationFailed'));
            })
        },
        //切换语言
        changeLanguage(lang){
            this.$i18n.locale = lang;
            localStorage.setItem('language',lang);
        },
        //查询当前用户是否拥有上下级关系
        getRelation(){
            getRelation({
                address:this.addr,
            }).then(res=>{
                if(res.code == 200){
                    let bindInfo=res.data || {};
                    if(bindInfo.isVip==1){  //激活成功
                    }else if(bindInfo.isVip==2){    //未激活
                        this.bindInfo.isVip='';//置空
                        this.findRelation();
                    }
                    this.bindInfo=bindInfo;
                }else{
                    // this.$Message.error(this.$t('operationFailed'));
                }
            }).catch(err=>{
                console.log(err);
            });
        },
        //区块链上查询是否激活成功
        async findRelation(){
            ContractUtil.exchange.at(this.blockchain.exchange).then(instance=>{
                instance.referrals(this.addr).then(res=>{
                    if(res!='0x0000000000000000000000000000000000000000'){
                        this.bindInfo.isVip=1;
                        this.sendDefineRelation();
                    }else{
                        this.bindInfo.isVip=2;
                    }
                })
            });
        }
    },
    computed:{
        blockchain(){
            return this.$store.state.blockchain;
        },
        addr(){
            return this.$store.state.app.wallet.address;
        },
        //钱包地址
        getwalletAddr(){
            return this.$store.state.app.wallet.address.substr(0,6)+"...."+this.$store.state.app.wallet.address.substr(38,4);
        },
        //是否初始化钱包
        isInitWallet() {
            return this.$store.state.app.wallet.isInit;
        },
        isShowLoginPop() {
            return this.$store.state.app.isShowLoginPop;
        },
    },
    watch:{
        //是否登录钱包
        'isInitWallet':{
            handler:function(val,value){
                if(val){
                    this.init();
                    this.getRelation();
                    //钱包图标
                    let walletActive=JSON.parse(sessionStorage.getItem('walletActive'));
                    this.tradeTypeIcon=(walletActive&&walletActive.img) || '';
                    //二维码
                    let url=document.URL;
                    this.myShareLink=url.split('?')[0]+`?addr=${this.addr}`;
                }else{
                }
            },
            immediate: true
        },
       "$route.path": {
            handler(val) {
                this.navigationActive=val.replace("/", "");
            },
            immediate: true
        },
        'token':{
            handler(val) {
                this.info={};
                if(val){
                    this.user_detail();
                }
            },
            immediate: true
        },
        'isShowLoginPop':{
            handler:function(val,value){
                if(val){
                    this.isconnectWallet=true;
                }
            },
            deep: false,
            immediate: true
        }
        
    },
};
</script>
