    // padding:0 10px;
    height:48px;.flexCenter;position:relative;
    // &:after{.afterLine;}
    >a{
        // color:@color;
    color: #c0c0c0;
    padding:0 15px;
    // padding:0 8px;
    height:32px;
    line-height: 32px;
    .flexCenter;border-radius:@border-radius-base;margin:0 5px;
        &:hover{
            // background: @graybg;color:@active-color;
            background-color: rgba(56, 97, 251, 0.1);
            // background: @graybg;
            color:#3861fb !important;
        }
    }
}
