<template>
    <Modal :closable="false" :transfer="false" :value="isShow" width="400" :mask-closable="true" @on-cancel="close()" >
        <!--菜单-->
        <div slot="header" class="CWMheader">
            <h3 class="center">{{$t("登录")}}</h3>
        </div>
        <!--内容-->
        <div class="CWMcontentMain">
            <Form :model="form">
                <FormItem label="账号">
                    <Input v-model="form.account" placeholder="请输入账号" class=""></Input>
                </FormItem>
                <FormItem label="密码">
                    <Input v-model="form.password" type="password" placeholder="请输入密码" class=""></Input>
                </FormItem>
                <!-- <FormItem class="">
                    
                </FormItem> -->
                <div class="flexCenter">
                    <Button type="primary" style="width:60%;" @click="login">登录</Button>
                <!-- <Button style="margin-left: 15px;min-width:80px;">重置</Button> -->
                </div>
            </Form>
        </div>
        <!--底部栏-->
        <div slot="footer" class="CWMfooter">
            <div>
                <!--关于钱包的描述-->
                <!-- <span>{{this.$t("aboutWallets")}}</span>
                <Icon size="14" type="md-arrow-up" /> -->
            </div>
            <div @click="close()"><span>{{$t("skipForNow")}}</span><Icon size="14" type="md-return-right" /></div>
        </div>
    </Modal>
</template>
<script>
import { sendMsg } from "@/api/http"
import { getWallets } from './const/const'
export default {
    name: '',
    model:{
        prop:'isShow',
        event:'change'
    },
    props: {
        //连接钱包弹窗，是否显示
        isShow:{
            type:Boolean,
            default:false
        },
    },
    components: {

    },
    data() {
        return {
            dataList:getWallets(),//钱包列表
            form: {
                account: '18582541446',
                password:'123456'
            }
        }
    },
    mounted() {
    },
    methods: {
        login(){
            if(!this.form.account){
                this.$Message.error(this.$t('请输入账号'));
                return;
            }
            if(!this.form.password){
                this.$Message.error(this.$t('请输入密码'));
                return;
            }
            this.$api.login(this.form).then(res => {
                // console.log('res',res)
                localStorage.setItem('token',res.accessToken);
                this.$emit("loginIn",false);
                this.close();
            });
        },
        //关闭
        close(){
            this.$store.state.app.isShowLoginPop=false;
            this.$emit("change",false);
        },
    },
    computed:{
        addr(){
            return this.$store.state.app.wallet.address;
        },
    },
    watch:{
        
    }
};
</script>
<style lang='less' scoped>
.ivu-modal-content{
    background: @white; overflow: auto;color:@text-color;
    >div{
        background-color: rgba(0, 0, 0, 0.5);border:none;padding:0;
    }
}
.CWMheader{color: @color;
    padding: 20px 0 0 0;
}
.CWMfooter{.flexBetween;
    >div{display:flex;align-items:center;
        >i{.ml5;}
        &:hover{cursor: pointer;color: @color;}
    }
}
.CWMcontentMain{
    background: @black;border-radius: 6px;min-height: 200px;padding:20px;
    .CWMCMitem{
        padding:0;border-radius: 6px;display:inline-block;.mr10;.mb10;
        
    }
}
.CWMCMImain{
    >span{
        display: flex;align-items: center;
    }
}
.walletBtn {
    font-family: Relative Pro;
    font-weight: 500;
    -webkit-font-feature-settings: "zero" 0;
    font-feature-settings: "zero" 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: @theme-color;
    color: #c3c2d4;
    border-radius: 6px;
    cursor: pointer;
    padding: 0 16px;
    border: none;
    white-space: nowrap;
    font-size: 14px;
    line-height: 18px;
    height: 36px;
    min-height: 36px;
    &:hover{opacity:0.8;}
}

</style>